import { Injectable } from '@angular/core';
import { IOperation, IResult, OperationService } from '@c8y/client';
import { gettext, OptionsService } from '@c8y/ngx-components';
import { Command, CommandDeliveryType, DeliveryType } from './device-shell.model';

@Injectable()
export class DeviceShellService {
  constructor(private optionsService: OptionsService, private operationService: OperationService) {}

  getDeliveryTypes(): DeliveryType[] {
    return [
      {
        name: gettext(CommandDeliveryType.DEFAULT),
        default: true
      },
      {
        name: gettext(CommandDeliveryType.SMS)
      }
    ];
  }

  canSendCommandsViaSMS(): Promise<boolean> {
    return this.optionsService.getSystemOption('messaging', 'provider', false);
  }

  createCommandOperation(
    deviceId: string,
    command: Command,
    deliveryType: CommandDeliveryType
  ): Promise<IResult<IOperation>> {
    const operation: IOperation = {
      deviceId,
      description: gettext('Execute shell command') + (command.name ? `: ${command.name}` : ''),
      deliveryType: deliveryType === CommandDeliveryType.SMS ? CommandDeliveryType.SMS : undefined,
      c8y_Command: {
        text: command.text
      }
    };
    return this.operationService.create(operation);
  }
}
