import { Directive, ElementRef, EventEmitter, Injector, Output } from '@angular/core';
import { UpgradeComponent } from '@angular/upgrade/static';

@Directive({
  selector: 'c8y-command-templates-directive'
})
export class CommandTemplates extends UpgradeComponent {
  @Output()
  close: EventEmitter<void>;

  @Output()
  dismiss: EventEmitter<void>;

  constructor(elementRef: ElementRef, injector: Injector) {
    super('c8yCommandTemplates', elementRef, injector);
  }
}
