import { NgModule } from '@angular/core';
import { gettext, HOOK_ROUTE, Route, ViewContext } from '@c8y/ngx-components';
import { DeviceShellGuard } from './device-shell.guard';
import { DeviceShellComponent, ShellModule } from './shell';

const DEVICE_SHELL_ROUTE: Route = {
  path: 'shell',
  context: ViewContext.Device,
  component: DeviceShellComponent,
  label: gettext('Shell'),
  icon: 'terminal',
  canActivate: [DeviceShellGuard]
};

@NgModule({
  imports: [ShellModule],
  providers: [
    DeviceShellGuard,
    {
      provide: HOOK_ROUTE,
      useValue: DEVICE_SHELL_ROUTE,
      multi: true
    }
  ]
})
export class DeviceShellModule {}
