import { Component, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { IManagedObject, IOperation, IResultList, OperationService } from '@c8y/client';
import {
  AlertService,
  gettext,
  ManagedObjectRealtimeService,
  OperationRealtimeService
} from '@c8y/ngx-components';
import { includes, isEmpty } from 'lodash-es';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';
import { BehaviorSubject, Observable, of, pipe, Subject } from 'rxjs';
import { filter, map, startWith, takeUntil } from 'rxjs/operators';
import { CommandTemplatesComponent } from '../command-templates';
import { Command, CommandDeliveryType, DeliveryType, DeviceShellService } from '../shared';

@Component({
  selector: 'c8y-device-shell',
  templateUrl: 'shell.component.html',
  providers: [OperationRealtimeService, ManagedObjectRealtimeService]
})
export class DeviceShellComponent implements OnInit, OnDestroy {
  device: IManagedObject = this.route.snapshot.parent.data.contextData;
  device$: Observable<IManagedObject> = of({} as any);
  deliveryTypes: Array<DeliveryType & { isSupportedByCommand?: boolean }> = (
    this.service.getDeliveryTypes() || []
  ).map(deliveryType => ({ ...deliveryType, isSupportedByCommand: true }));
  command: Command = {} as Command;
  smsEnabled: boolean = false;
  sendingCommand$: BehaviorSubject<boolean> = new BehaviorSubject(false);
  operations: IResultList<IOperation>;
  filterPipe = pipe(
    map((operations: IOperation[]) =>
      (operations || []).filter((operation: IOperation) => !!operation.c8y_Command)
    )
  );

  executeViaLabel = gettext('Execute via ({{deliveryType}})');

  private destroyed$: Subject<void> = new Subject();

  constructor(
    public service: DeviceShellService,
    public operationRealtime: OperationRealtimeService,
    private moRealtime: ManagedObjectRealtimeService,
    private operationService: OperationService,
    private route: ActivatedRoute,
    private modalService: BsModalService,
    private alertService: AlertService
  ) {}

  async ngOnInit(): Promise<void> {
    this.smsEnabled = await this.service.canSendCommandsViaSMS();

    this.device$ = this.moRealtime.onUpdate$(this.device.id).pipe(startWith(this.device));

    this.operations = await this.operationService.list({
      deviceId: this.device.id,
      fragmentType: 'c8y_Command',
      dateFrom: new Date(0).toISOString(),
      dateTo: new Date().toISOString(),
      pageSize: 50,
      withTotalPages: true,
      revert: true
    });

    this.operationRealtime
      .onCreate$(this.device.id)
      .pipe(takeUntil(this.destroyed$))
      .subscribe(() => this.alertService.success(gettext('Command sent.')));

    this.operationRealtime
      .onUpdate$(this.device.id)
      .pipe(
        filter(op => op.failureReason !== 'Operation cancelled by user.'), // avoid duplicate alerts
        takeUntil(this.destroyed$)
      )
      .subscribe(() => this.alertService.success(gettext('Command status updated.')));
  }

  getPredefinedCommand() {
    const modal: BsModalRef<CommandTemplatesComponent> =
      this.modalService.show(CommandTemplatesComponent);
    modal.content.onTemplateSelected.pipe(takeUntil(this.destroyed$)).subscribe(result => {
      this.command = { ...(result as any).commandTemplate } as Command;
      this.deliveryTypes = this.deliveryTypes.map(deliveryType => ({
        ...deliveryType,
        isSupportedByCommand:
          isEmpty(this.command.deliveryTypes) ||
          includes(this.command.deliveryTypes, deliveryType.name)
      }));
    });
  }

  resetSupportedDeliveryTypes(): void {
    this.deliveryTypes = (this.service.getDeliveryTypes() || []).map(deliveryType => ({
      ...deliveryType,
      isSupportedByCommand: true
    }));
  }

  async execute(commandDeliveryType): Promise<void> {
    const useSMS = commandDeliveryType === CommandDeliveryType.SMS;
    if (useSMS && !this.smsEnabled) {
      this.alertService.warning(gettext('SMS transport is not configured.'));
      return;
    }
    this.sendingCommand$.next(true);

    await this.service.createCommandOperation(this.device.id, this.command, commandDeliveryType);

    this.command.text = '';
    this.resetSupportedDeliveryTypes();
    this.sendingCommand$.next(false);
  }

  ngOnDestroy(): void {
    this.destroyed$.next();
    this.destroyed$.complete();
  }
}
